
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name:"addrChange",
components:{
Cell,
Group,
XInput,
XSwitch,
Selector,
XButton
},
data(){
return{
name:this.$route.params.name,
phone:this.$route.params.phone,
addres:this.$route.params.addres,
partAddres:this.$route.params.partAddres
}
},
methods:{
change(){
if(this.name!=this.$route.params.name){
let arr = JSON.parse(localStorage.addrChange);
arr.name = this.name;
arr.checked = false
arr.namePhone = this.name+','+arr.phone;
localStorage.addrChange = JSON.stringify(arr)
this.$store.commit('saveAddres',arr)
let _this = this  
_this.$vux.alert.show({
content: '地址修改成功',
onHide () {
_this.$router.push({name:'profile'});    
_this.name = "";
_this.phone = "";
_this.addres = "";0
_this.partAddres = "";
}
})
}else if(this.phone!=this.$route.params.phone){
let arr = JSON.parse(localStorage.addrChange);
arr.phone = this.phone;
arr.checked = false
arr.namePhone = arr.name+','+this.phone;
localStorage.addrChange = JSON.stringify(arr)
this.$store.commit('saveAddres',arr)
let _this = this  
_this.$vux.alert.show({
content: '地址修改成功',
onHide () {
_this.$router.push({name:'profile'});    
_this.name = "";
_this.phone = "";
_this.addres = "";
_this.partAddres = "";
}
})
}else if(this.addres!=this.$route.params.addres){
let arr = JSON.parse(localStorage.addrChange);
arr.addr = this.addres;
arr.checked = false
arr.addres = this.addres+','+arr.partAddres;
localStorage.addrChange = JSON.stringify(arr)
this.$store.commit('saveAddres',arr)
let _this = this
_this.$vux.alert.show({
content: '地址修改成功',
onHide () {
_this.$router.push({name:'profile'});    
_this.name = "";
_this.phone = "";
_this.addres = "";
_this.partAddres = "";
}
})
}else if(this.partAddres!=this.$route.params.partAddres){
let arr = JSON.parse(localStorage.addrChange);
arr.partAddres = this.partAddres;
arr.addres = arr.addr+','+this.partAddres;
localStorage.addrChange = JSON.stringify(arr)
this.$store.commit('saveAddres',arr)
let _this = this
_this.$vux.alert.show({
content: '地址修改成功',
onHide () {
_this.$router.push({name:'profile'});    
_this.name = "";
_this.phone = "";
_this.addres = "";
_this.partAddres = "";
}
})
}else{
this.$vux.alert.show({
title: '注意',
content: '您并未进行任何修改',
})
}
}
},
created(){
this.$store.commit('keepAlive','addres')
},
beforeRouteLeave (to, from, next) {
if(to.path == '/profile'){
this.$store.commit('changekeepAlive','profile')
}
next()
}
}



